@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/gothampro/gothampro-regular.woff2"), url("../fonts/gothampro/gothampro-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/gothampro/gothampro-light.woff2"), url("../fonts/gothampro/gothampro-light.woff");
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/gothampro/gothampro-bold.woff2"), url("../fonts/gothampro/gothampro-bold.woff");
  font-display: swap;
}
@font-face {
  font-family: "GetVoIP Grotesque";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/getvoipgrotesque/getvoipgrotesque.woff2"), url("../fonts/getvoipgrotesque/getvoipgrotesque.woff");
  font-display: swap;
}
@font-face {
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/floripa/floripa.woff2"), url("../fonts/floripa/floripa.woff");
  font-display: swap;
}
/* Fonts */
/* Breakpoints */
/* Colors */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Gotham Pro", "Arial", sans-serif;
  font-weight: 300;
  font-size: 20px;
  line-height: 26px;
  color: #1c1c1c;
  background-color: #ffffff;
}

/* Fonts */
/* Breakpoints */
/* Colors */
.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 24px;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .container {
    padding: 0 48px;
  }
}

.button {
  display: block;
  padding: 19px 18px 18px 18px;
  font-family: "Gotham Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  transition: 0.3s ease-out;
}
.button--primary {
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: transparent;
}
.button--primary:hover, .button--primary:active, .button--primary:focus {
  color: #1c1c1c;
  background-color: #ffffff;
}
.button--accent {
  color: #ffffff;
  border: 1px solid #1c1c1c;
  background-color: #1c1c1c;
}
.button--accent:hover, .button--accent:active, .button--accent:focus {
  background-color: transparent;
  color: #1c1c1c;
}
.button--secondary {
  color: #1c1c1c;
  border: 1px solid #1c1c1c;
  background-color: #f4f4f4;
}
.button--secondary:hover, .button--secondary:active, .button--secondary:focus {
  color: #ffffff;
  background-color: #1c1c1c;
}

.main-header {
  background-color: #1c1c1c;
  z-index: 4;
  transition: 1s ease;
}
@media (max-width: 767px) {
  .main-header {
    position: sticky;
    top: 0;
    max-height: 72px;
  }
}
@media (min-width: 768px) {
  .main-header {
    padding: 16px 24px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 16px 48px 16px 48px;
  }
}
@media (max-width: 767px) {
  .main-header--opened .main-header__nav {
    transform: translateY(0);
    transition: transform 1s ease-out;
  }
}
.main-header--opened .main-header__toggle-line:nth-child(1) {
  transform: scaleX(0);
}
.main-header--opened .main-header__toggle-line:nth-child(2) {
  transform: rotate(45deg);
}
.main-header--opened .main-header__toggle-line:nth-child(3) {
  transform: rotate(-45deg);
}
.main-header--opened .main-header__toggle-line:nth-child(4) {
  transform: scaleX(0);
}

@media (min-width: 768px) {
  .main-header__wrapper {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    max-width: 1344px;
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .main-header__nav {
    position: absolute;
    left: 0;
    right: 0;
    transform: translateY(-150%);
    background-color: #1c1c1c;
    z-index: 3;
    transition: opacity 0.3s ease-out, transform 1s ease-out;
  }
}

.main-header__wrapper-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 72px;
  padding: 22px 24px 18px 24px;
}
@media (min-width: 768px) {
  .main-header__wrapper-top {
    min-height: unset;
    width: unset;
    flex-grow: 1;
    margin: 0 12px 0 0;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper-top {
    margin: 0 48px 0 0;
  }
}

.main-header__logo {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header__logo {
    width: auto;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    margin: 9px 0 0 0;
  }
}

.main-header__logo-img {
  object-fit: contain;
}
@media (min-width: 768px) {
  .main-header__logo-img {
    min-width: 150px;
    height: auto;
    max-height: 26px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo-img {
    min-width: 240px;
    height: auto;
    max-height: 35px;
  }
}

.main-header__toggle {
  position: relative;
  width: 36px;
  height: 36px;
  margin: -5px 0 0 0;
  padding: 9px;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #ffffff;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle-line {
  display: block;
  position: absolute;
  left: 10px;
  right: 10px;
  height: 1px;
  background-color: #ffffff;
  transition: 0.3s ease-out;
}
.main-header__toggle-line:nth-child(1) {
  top: 11px;
}
.main-header__toggle-line:nth-child(2) {
  top: 16px;
}
.main-header__toggle-line:nth-child(3) {
  top: 16px;
}
.main-header__toggle-line:nth-child(4) {
  top: 21px;
}

.main-nav {
  display: flex;
  flex-direction: column;
  min-height: 742px;
  padding: 52px 24px 48px 24px;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    min-height: unset;
    width: 100%;
    padding: 0;
  }
}

.main-nav__button {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: block;
    max-width: 249px;
    margin: 0 0 0 auto;
    font-size: 12px;
  }
}
@media (min-width: 1440px) {
  .main-nav__button {
    width: 100%;
    padding: 16px 18px 16px 18px;
    font-size: 14px;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  gap: 45px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    align-items: center;
    gap: 24px;
    margin: 6px 0;
    padding: 0 0 0 12px;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    gap: 48px;
    margin: 8px 0;
    padding: 0 0 0 48px;
  }
}

.main-nav__link {
  font-weight: 400;
  font-size: 24px;
  line-height: 23px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 12px;
    line-height: 13px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    font-size: 14px;
  }
}

.main-nav__contacts {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 16px;
  margin: auto 0 0 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__contacts {
    display: none;
  }
}

.main-nav__contacts-item {
  width: 100%;
}
.main-nav__contacts-item--icon {
  width: 52px;
}
.main-nav__contacts-item:nth-child(2) {
  width: calc(100% - 52px - 16px);
}

.main-nav__contacts-link {
  font-weight: 400;
}
.main-nav__contacts-link--icon {
  max-height: 52px;
  max-width: 52px;
}

.promo {
  position: relative;
}
.promo .container {
  display: grid;
}

.promo__slider.swiper {
  min-width: 0;
  max-width: 100%;
  margin: 24px 0 0 0;
}
@media (min-width: 768px) {
  .promo__slider.swiper {
    margin: 48px 0 0 0;
    padding: 0 0 30px 0;
  }
}

.promo__picture-wrapper {
  background-image: linear-gradient(180deg, #ebeaee 0%, #edecef 13.75%, #f3f3f3 22.73%, #f3f3f3 31.39%, #f1f1f1 52.88%, #f3f3f3 62.26%, #f1f0f5 100%);
}
@media (min-width: 768px) {
  .promo__picture-wrapper {
    background-image: none;
    background-color: #ededf1;
  }
}
.promo__picture-wrapper--dark {
  background-color: #070c10;
  background-image: none;
}
.promo__picture-wrapper--light {
  background-color: #babbbd;
  background-image: none;
}
@media (min-width: 768px) {
  .promo__picture-wrapper {
    grid-column: 1/-1;
    grid-row: 1/-1;
  }
}

@media (min-width: 768px) {
  .promo__wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
}

.promo__img {
  display: block;
  width: 100%;
  margin: 0 0 44px 0;
  object-fit: contain;
  max-height: 340px;
  object-position: right 0 top 50%;
  image-rendering: -webkit-optimize-contrast;
}
@media (min-width: 768px) {
  .promo__img {
    max-height: 374px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .promo__img {
    max-height: 698px;
  }
}

.promo__title-wrapper {
  position: relative;
  overflow: hidden;
}

.promo__title {
  margin: 0 0 12px 0;
  font-family: "GetVoIP Grotesque", "Arial", sans-serif;
  font-size: 36px;
  line-height: 44px;
  text-transform: uppercase;
  transform: translateY(100%);
  transition: transform 1.5s ease, opacity 1s ease;
}
@media (min-width: 768px) {
  .promo__title {
    margin: 0 0 20px 0;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .promo__title {
    font-size: 58px;
    line-height: 62px;
  }
}

.promo__wrapper.swiper-slide-active .promo__title {
  transform: translateY(0);
}

.promo__wrapper.swiper-slide-prev .promo__title {
  transform: translateY(-100%);
  opacity: 0;
}

.promo__lead {
  position: relative;
  margin: 0 0 40px 0;
  font-size: 18px;
  line-height: 24px;
  overflow: hidden;
  z-index: 3;
}
@media (min-width: 768px) {
  .promo__lead {
    max-width: 250px;
    margin: 0 0 25px 0;
    font-size: 14px;
    line-height: 16px;
  }
}
@media (min-width: 1040px) {
  .promo__lead {
    max-width: 420px;
  }
}
@media (min-width: 1440px) {
  .promo__lead {
    max-width: 445px;
    margin: 0 0 20px 0;
    font-size: 20px;
    line-height: 26px;
  }
}

.promo__text-wrapper {
  overflow: hidden;
  z-index: 2;
}
@media (min-width: 768px) {
  .promo__text-wrapper {
    grid-column: 1/-1;
    grid-row: 1/-1;
    width: 42%;
    height: 63%;
    align-self: end;
    margin-bottom: -2px;
    padding: 20px 20px 20px 0;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .promo__text-wrapper {
    width: 570px;
    height: 441px;
    padding: 40px 52px 45px 0;
  }
}

@media (min-width: 768px) {
  .promo__button {
    max-width: 410px;
  }
}

.promo__pagination.swiper-pagination {
  position: absolute;
  top: 340px;
  right: 0;
  max-height: 40px;
}
@media (min-width: 768px) {
  .promo__pagination.swiper-pagination {
    top: 374px;
    width: 258px;
    left: unset;
    max-height: unset;
  }
}
@media (min-width: 1440px) {
  .promo__pagination.swiper-pagination {
    top: 698px;
  }
}
.promo__pagination.swiper-pagination .swiper-pagination-bullet {
  position: relative;
  width: 30.8%;
  border-radius: 0;
  background-color: transparent;
  border: 1px solid #d4d4d4;
  opacity: 1;
  transition: 0.3s ease-out;
}
.promo__pagination.swiper-pagination .swiper-pagination-bullet:hover, .promo__pagination.swiper-pagination .swiper-pagination-bullet:focus {
  border-color: #1c1c1c;
}
@media (min-width: 768px) {
  .promo__pagination.swiper-pagination .swiper-pagination-bullet {
    width: 78px;
  }
}
.promo__pagination.swiper-pagination .swiper-pagination-bullet-active {
  position: relative;
}
.promo__pagination.swiper-pagination .swiper-pagination-bullet-active::after {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  animation: change-background 6s linear;
  background-color: #1c1c1c;
  border-color: #1c1c1c;
  transform-origin: 0 0;
  box-sizing: content-box;
}

@keyframes change-background {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}
.steps {
  padding: 100px 0 0 0;
}
@media (min-width: 1440px) {
  .steps {
