}

.steps__title {
  margin: 0 0 32px 0;
  font-family: "GetVoIP Grotesque", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
}
@media (min-width: 1440px) {
  .steps__title {
    margin: 0 0 56px 10px;
    font-size: 30px;
    line-height: 30px;
  }
}

.steps__list {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .steps__list {
    grid-template-rows: unset;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1440px) {
  .steps__list {
    gap: 16px;
  }
}

.steps__item {
  position: relative;
  display: flex;
  flex-direction: column;
}
.steps__item::after {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 72px;
  height: 72px;
  font-family: "GetVoIP Grotesque", "Arial", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  -webkit-text-stroke: 1px #1c1c1c;
