}
.steps__item--first::after {
  content: "1";
}
.steps__item--second::after {
  content: "2";
}
.steps__item--third::after {
  content: "3";
}

.steps__img {
  display: block;
  width: 100%;
  margin: 0 0 25px 0;
}

.steps__item-title {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
}
@media (min-width: 1440px) {
  .steps__item-title {
