  color: #1c1c1c;
  background-color: transparent;
  border: 1px solid #1c1c1c;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  transition: 0.3s ease;
}
