  margin: 0 0 37px 0;
}

.modal__button {
  width: 100%;
  padding: 16px 18px 16px 18px;
  font-size: 14px;
}

.modal__img-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 90%;
  max-height: 90%;
}

.modal__form-input-wrapper {
  position: relative;
}

.modal__button-reset {
  position: absolute;
  top: 50%;
  right: 15px;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-50%);
  padding: 0;
  background-color: transparent;
  border: none;
  transition: opacity 0.3s ease-out;
  cursor: pointer;
}

.modal__form-input {
  width: 100%;
}

.modal__form-input:not(:placeholder-shown) + .modal__button-reset {
  opacity: 1;
}

.modal__form-input.error + .modal__button-reset {
  color: #e13017;
  opacity: 1;
}

